.login-register {
	background-image: linear-gradient(235deg, #565867, #191a25);
	height: 100vh;
	margin: 0;
	padding: 0;
}

.login-register-panel {
    width: 650px;
    height: 525px;
    background-color: var(--surface-a);
    position: absolute;
    left: calc(50% - 325px);
    top: calc(50% - 260px);
    border: 1px solid #d5b8b8;
    padding: 5px;
    border-radius: 10px;
    box-shadow: 3px 3px #e2e2e2;
}

img {
    border-style: none;
    border-radius: 5px;
}

.full-btn {
	width: 100%;
}